            <i class="fa fa-battery-full"></i>
          </span>
        </div>
      </div>
      <div class="header__bottom">
        <div class="header__column">
          <span class="header__text">{{menu}}</span>
        </div>
        <div class="header__column">
          <span class="header__text">
            {{title}}
            <span class="header__number">{{num}}</span>
