<template>
  <li class="chats__chat">
    <a href="chat.html">
      <div class="chat__content">
        <img :src="chat.userImage">
        <div class="chat__preview">
          <h3 class="chat__user">{{chat.username}}</h3>
          <span class="chat__last-message">{{chat.msg}}</span>
        </div>
      </div>
      <span class="chat__date-time">{{chat.timeHr}}:{{chat.timeMin}}</span>
    </a>
  </li>
</template>

<!--props-->

<script>
export default {
  name: 'ChatList',
  props: {
    chat: Object
  }
}
</script>

